
import React from 'react';
import "react-table/react-table.css"
import Const from '../services/Constants';
import AjaxService from '../services/AjaxService';
import DataAccessService from '../services/DataAccessService';
import { SetStateInput, NavLinkButton } from '../parts/TinyParts';
import History from '../services/RouteHistoryProvider';

class OrderDetails extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isDisabled: false
    };
  }

  handleSubmit = () => {
    let st = this.state;
    if (!st.name || !st.email || !st.address || !st.phone) {
      window.alert("–ó–∞–¥—ä–ª–∂–∏—Ç–µ–ª–Ω–∏ –ø–æ–ª–µ—Ç–∞: –ò–º–µ, email, –∞–¥—Ä–µ—Å, —Ç–µ–ª–µ—Ñ–æ–Ω!");
      return;
    }

    let confirm = window.confirm("–ú–æ–ª—è, –ø–æ—Ç–≤—ä—Ä–¥–µ—Ç–µ –ø–æ—Ä—ä—á–∫–∞—Ç–∞!");
    if (!confirm) {
      return;
    }
    let allItems = DataAccessService.getCurrentOrder();
    let arr = [];

    Object.keys(allItems)
      .forEach((key) => {
        arr.push(allItems[key])
      });

    arr = arr
      .filter(one => { return one.quantity !== 0 })
      .map(one => {
        return {
          quantity: one.quantity,
          price: one.orderedItem.price,
          id: one.orderedItem.id
        };
      });

    let data = {
      name: this.state.name,
      email: this.state.email,
      address: this.state.address,
      phone: this.state.phone,
      more: this.state.more,
      order: arr
    };

    this.setState({ isDisabled: true });
    let promise = AjaxService.doPost(Const.URLS.ORDER, data, true);
    promise.then((data) => {
      History.goTo("/order-result", { "result": "–ü–æ—Ä—ä—á–∫–∞ –Ω–æ–º–µ—Ä: " + data.data + " –±–µ—à–µ –ø—Ä–∏–µ—Ç–∞." });
    }).catch((e) => {
      this.setState({ isDisabled: false });
      History.goTo("/error", e);
    });
  };

  render() {
    return (
      <div className="text-left text-wrap">
        <label>–î–µ—Ç–∞–π–ª–∏ –∑–∞ –¥–æ—Å—Ç–∞–≤–∫–∞—Ç–∞:</label>
        <br />
        <p />
        <br />
        <div className="text-left text-wrap">
          <SetStateInput stateFieldName="name" statefulObject={this} label="–ò–ºe" />
          <div className="row">
            <div className="col">
              <SetStateInput stateFieldName="phone" statefulObject={this} label="–¢–µ–ª–µ—Ñ–æ–Ω" type="tel" />
            </div>
            <div className="col">
              <SetStateInput stateFieldName="email" statefulObject={this} label="e-mail" type="email" />
            </div>
          </div>
          <SetStateInput stateFieldName="address" statefulObject={this} label="A–¥—Ä–µ—Å –∑–∞ –¥–æ—Å—Ç–∞–≤–∫–∞" />
          <SetStateInput stateFieldName="more" statefulObject={this} label="–û—â–µ –Ω–∞—Å–æ–∫–∏" />
          <br />
          <div className="text-center">
            <div className=" btn-group ">
              <NavLinkButton to="/basket" label="–û–±—Ä–∞—Ç–Ω–æ" />
              <div>
                <button type="submit" className="btn btn-primary ml-1" disabled={this.state.isDisabled} onClick={this.handleSubmit}>–ü–æ—Ä—ä—á–∞–π!</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default OrderDetails;