import React from 'react';
import "react-table/react-table.css"
import ReactTable from "react-table";
import { SetStateInput } from '../parts/TinyParts'
import DataAccessService from '../services/DataAccessService';
import { prepareCalculationPricelist } from '../customer/Basket';
import History from '../services/RouteHistoryProvider';

class ReviewOrder extends React.Component {
  constructor(props) {
    super(props);
    this.allOrders = props?.location?.state?.allOrders || [];
    let order = props?.location?.state?.order || {};
    this.index = props?.location?.state?.index || 0;
    this.state = this.makeStateObject(order);
  }

  makeStateObject = (order) => {
    let priceMap = DataAccessService.getPriceListObj();
    let ordersArr = order?.order || [];

    for (let index = 0; index < ordersArr.length; index++) {
      let orderItem = ordersArr[index]; // one item from the order
      let priceListItem = priceMap[orderItem.id] || {};
      orderItem.title = priceListItem.title
      let warning = "";
      if (priceListItem.price !== orderItem.price) {
        warning = "‚ö†Ô∏è –†–∞–∑–ª–∏—á–Ω–∏ —Ü–µ–Ω–∏: —Ü–µ–Ω–æ–≤–∞ –ª–∏—Å—Ç–∞: " + priceListItem.price + "; –ø–æ—Ä—ä—á–∫–∞: " + orderItem.price + "; ";
      }
      orderItem.info = warning + priceListItem.info;
    }

    return {
      address: order.address || " ",
      email: order.email || " ",
      more: order.more || " ",
      hour: new Date(parseInt(order.date + "000")).toLocaleString() || " ",
      name: order.name || " ",
      phone: order.phone || " ",
      orderedItems: ordersArr || []
    }
  }

  setTheState = (index) => {
    if (index === undefined) {
      return;
    }

    let order = this.allOrders[index] || {};
    let newState = this.makeStateObject(order);
    this.setState(newState)
  }

  prev = () => {
    if (this.index < 1) {
      return;
    }
    this.index--;
    this.setTheState(this.index);
  }

  next = () => {
    if (this.index >= this.allOrders.length - 1) {
      return;
    }
    this.index++;
    this.setTheState(this.index);
  }

  goToView = () => {
    History.goTo("/select-orders")
  }

  render() {
    let data = this.state.orderedItems;

    data = prepareCalculationPricelist(data);

    let present = [
      {
        columns: [
          {
            Header: "–ê—Ä—Ç–∏–∫—É–ª",
            accessor: "title"
          },
          {
            Header: "–ò–Ω—Ñ–æ",
            accessor: "info",
            maxWidth: 250
          },
          {
            Header: "–¶–µ–Ω–∞, –æ–ø–∞–∫–æ–≤–∫–∞",
            accessor: "price",
            maxWidth: 140
          },
          {
            Header: "–ö-–≤–æ",
            accessor: "quantity",
            maxWidth: 75
          },
          {
            Header: "–û–±—â–æ",
            accessor: "total",
            maxWidth: 100
          }
        ]
      }
    ];

    return (
      <div>
        <div className="text-center">
          <div className=" btn-group ">
            <div className="text-left">
              <button className="btn btn-primary mr-4" onClick={this.goToView} >–û–±—Ä–∞—Ç–Ω–æ</button>
            </div>
            <div className="mr-1">
              <button className="btn btn-primary" onClick={() => this.prev()} >‚óÄÔ∏è</button>
            </div>
            <div >
              <button className="btn btn-primary" onClick={() => this.next()} >‚ñ∂Ô∏è</button>
            </div>
          </div>
          <br />
          <div className="row">
            <div className="col">
              <SetStateInput stateFieldName="name" statefulObject={this} label="–ò–º–µ" disabled={true} />
            </div>
            <div className="col">
              <SetStateInput stateFieldName="phone" statefulObject={this} label="–¢–µ–ª–µ—Ñ–æ–Ω" disabled={true} />
            </div>
            <div className="col">
              <SetStateInput stateFieldName="email" statefulObject={this} label="e-mail" disabled={true} />
            </div>
            <div className="col">
              <SetStateInput stateFieldName="hour" statefulObject={this} label="–ß–∞—Å –Ω–∞ –ø–æ—Ä—ä—á–∫–∞" disabled={true} />
            </div>
          </div>
          <div className="row">
            <div className="col">
              <SetStateInput stateFieldName="address" statefulObject={this} label="–ê–¥—Ä–µ—Å" disabled={true} />
            </div>
            <div className="col">
              <SetStateInput stateFieldName="more" statefulObject={this} label="–î–æ–ø—ä–ª–Ω–∏—Ç–µ–ª–Ω–∞ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è" disabled={true} />
            </div>
          </div>
        </div>
        <br />
        <ReactTable className="-striped -highlight"
          data={data}
          columns={present}
          sortable={false}
          defaultPageSize={data.length}
          pageSize={data.length}
          showPagination={false}
        />
        <br />
        <div className="text-center">
          <div className="btn-group">
            <button className="btn btn-primary mr-1" title="–û–±—Ä–∞—Ç–Ω–æ" onClick={this.goToView} >–û–±—Ä–∞—Ç–Ω–æ</button>
          </div>
        </div>
      </div>
    )
  }
}

export default ReviewOrder;